
.a-list{width:100%;display:-ms-flexbox;display:flex;-ms-flex-pack:start;justify-content:flex-start;-ms-flex-align:start;align-items:flex-start;-ms-flex-wrap:wrap;flex-wrap:wrap;margin-bottom:50px
}
.a-list article{width:375px;height:360px;margin:10px;padding:5px;box-shadow:0 15px 30px rgba(0,0,0,0.2);animation:showA 1.5s ease-out 1
}
@keyframes showA{
0%{opacity:0;transform:translate3d(0, 50px, 0)
}
100%{opacity:1;transform:translate3d(0, 0, 0)
}
}
.a-list article .article a.photo{height:0;width:100%;display:block;overflow:hidden;position:relative;margin-bottom:10px;padding-bottom:55.71%;cursor:pointer
}
.a-list article .article a.photo span{display:block;position:absolute;width:100%;height:100%;opacity:1;background-size:cover;background-position:center;transition:opacity 0.3s ease-in
}
.a-list article .article .article-meta{height:23px;line-height:23px;overflow:hidden
}
.a-list article .article .article-meta p{float:left;color:#bbb
}
.a-list article .article .article-meta a{text-decoration:none;color:#62bbc3
}
.a-list article .article .article-meta .article-link{margin-right:6px
}
.a-list article .article .article-title{margin-bottom:10px;font-size:22px;font-weight:400;line-height:1.5em;word-wrap:break-word;overflow:hidden;height:33px
}
.a-list article .article .article-title a{text-decoration:none;color:#444;transition:color 0.3s ease-in
}
.a-list article .article .article-excerpt{position:relative;color:#aaa;font-size:16px;line-height:20px;overflow:hidden;height:80px
}
.a-list article:hover a.photo span{opacity:0.7
}
.a-list article:hover .article-title a{color:#62bbc3
}
@media screen and (max-width: 1480px){
.a-list article{width:330px;height:335px
}
.a-list article .article .article-excerpt{height:75px;font-size:14px
}
}



