
.tag-list{margin:50px;min-width:200px
}
.tag-item{display:inline-block;margin:10px;margin-bottom:20px
}
.tag-item a{position:relative;padding:10px 20px;border:1px solid #d9d9d9;border-radius:20px;box-sizing:border-box;font-size:15px;font-weight:normal;line-height:1.5;color:white;border-color:#e78170;background:#e78170;box-shadow:0 15px 30px rgba(0,0,0,0.1);text-decoration:none
}



