
.m-loading{position:relative;margin:auto;margin-top:20px;width:200px;height:100px
}
.m-loading span{position:absolute;display:inline-block;width:15px;height:15px;border-radius:15px;background-color:white
}
.m-loading p{color:black;padding-top:20px;margin-left:25px
}
.m-loading span.row1{left:0px;background-color:#e14545;animation:row1 1s ease-out infinite
}
.m-loading span.row2{left:25px;background-color:#fcb421
}
.m-loading span.row3{left:50px;background-color:#1aadc0
}
.m-loading span.row4{left:75px;background-color:#aade26
}
.m-loading span.row5{left:100px;background-color:#5e5b9b;animation:row5 1s ease-out infinite
}
@keyframes row1{
0%{transform:translate3d(0, 0, 0)
}
50%{transform:translate3d(-10px, -30px, 0)
}
100%{transform:translate3d(0, 0, 0)
}
}
@keyframes row5{
0%{transform:translate3d(10px, -30px, 0)
}
50%{transform:translate3d(0, 0, 0)
}
100%{transform:translate3d(10px, -30px, 0)
}
}



