
.loading{position:absolute;width:100%;height:100%;top:0;left:0;background-color:rgba(0,0,0,0.6)
}
.loading .show-loading{position:absolute;left:50%;top:30%;width:200px;height:100px
}
.loading .show-loading span{position:absolute;display:inline-block;width:15px;height:15px;border-radius:15px;background-color:white
}
.loading .show-loading p{color:white;margin-top:30px;margin-left:25px
}
.loading .show-loading span.row1{left:0px;background-color:#e14545;animation:row1 1s ease-out infinite
}
.loading .show-loading span.row2{left:25px;background-color:#fcb421
}
.loading .show-loading span.row3{left:50px;background-color:#1aadc0
}
.loading .show-loading span.row4{left:75px;background-color:#aade26
}
.loading .show-loading span.row5{left:100px;background-color:#5e5b9b;animation:row5 1s ease-out infinite
}
@keyframes row1{
0%{transform:translate3d(0, 0, 0)
}
50%{transform:translate3d(-10px, -30px, 0)
}
100%{transform:translate3d(0, 0, 0)
}
}
@keyframes row5{
0%{transform:translate3d(10px, -30px, 0)
}
50%{transform:translate3d(0, 0, 0)
}
100%{transform:translate3d(10px, -30px, 0)
}
}



